/*global kakao*/
import React, { useEffect, useState, useMemo, useCallback } from 'react';
// import styled from 'styled-components';
import WrapPage from '../../components/WrapPage';

export default ( {history, location} ) => {
    
    return (
        <WrapPage>
            <AddressSetting history={history} location={location}/>
        </WrapPage>
    )
}

const AddressSetting = ({history, location}) => {

    const onClickBackBtn = (e) => {
        e.preventDefault();
        history.goBack();
    };

    
    //   
    const [cntrLat, setCntrLat] = useState(0.00);
    const [cntrLon, setCntrLon] = useState(0.00);

    const [latitude, setLatitude]= useState(0.00);
    const [longitude, setLongitude] = useState(0.00);

    const [userAddr, setUserAddr] = useState('');
    const [userRoadAddr, setUserRoadAddr] = useState('');
    
    var markerPosition;
    var marker;
    var latlng = new kakao.maps.LatLng();
    
    useEffect(() => {

        console.log(location.address)
        
        //     
        if(location.address != null) {
            // -  
            var geocoder = new kakao.maps.services.Geocoder();

            geocoder.addressSearch(location.address, function(result, status) {

                 if (status === kakao.maps.services.Status.OK) {
            
                    var latlng = new kakao.maps.LatLng(result[0].y, result[0].x);

                    // setCntrLat(latlng.getlat);
                    // setCntrLon(latlng.getlng);
                    // setLatitude(latlng.getlat);
                    // setLongitude(latlng.getlng);

                    setCntrLat(latlng.Ha);
                    setCntrLon(latlng.Ga);
                    setLatitude(latlng.Ha);
                    setLongitude(latlng.Ga);
                }
            });    
        }
        //      
        else if (navigator.geolocation) {
            
            navigator.geolocation.getCurrentPosition(function(position) {
                setCntrLat(position.coords.latitude);
                setCntrLon(position.coords.longitude);
                setLatitude(position.coords.latitude);
                setLongitude(position.coords.longitude);

            }, function(error) {
              console.error(error);
            }, {
              enableHighAccuracy: false,
              maximumAge: 0,
              timeout: Infinity
            });
        } else {
            setLatitude(33.450701);
            setLongitude(126.570667);
            alert('GPS  ');
        }
        
        if(cntrLat != 0 && cntrLon != 0) {

            // map options
            let container = document.getElementById("myMap");
            let options = {
                center: new kakao.maps.LatLng(cntrLat, cntrLon),
                level: 3
            };
            let map = new window.kakao.maps.Map(container, options);

            markerPosition = new kakao.maps.LatLng(cntrLat, cntrLon); 
            marker = new kakao.maps.Marker({
                position: markerPosition
            });
            
            var infowindow = new kakao.maps.InfoWindow({
                content: '<div style="width:150px;text-align:center;padding:6px 0;">?</div>'
            });

            marker.setMap(map);
            marker.setDraggable(true);
            infowindow.open(map, marker);

            //   info window
            kakao.maps.event.addListener(marker, 'dragstart', function(mouseEvent) {

                infowindow.close();
            });

            //  end / state 
            kakao.maps.event.addListener(marker, 'dragend', function(mouseEvent) {

                //   
                latlng = marker.getPosition();

                setLatitude(latlng.getLat());
                setLongitude(latlng.getLng());

                infowindow.open(map, marker);
            });
        }

    }, [cntrLon]);


    //  state    
    useEffect(() => {

        // -  
        var geocoder = new kakao.maps.services.Geocoder();
        
        geocoder.coord2Address(longitude, latitude, (result, status) => {

            if( status === 'ZERO_RESULT') {
                console.log('    .  .');
            } else if (status === kakao.maps.services.Status.OK && result.length > 0
                 && result[0].address != null ) {

                    setUserAddr(result[0].address.address_name);
                
                    if(result[0].road_address == null) {
                        setUserRoadAddr('');
                    } else 
                        setUserRoadAddr(result[0].road_address.address_name);

                    //     API  
                    // [{}]
                    // 0:
                    // address: {address_name: "   170-5", region_1depth_name: "", region_2depth_name: "", region_3depth_name: "", mountain_yn: "N", }
                    // road_address: null
                    // __proto__: Object
                    // length: 1
                    // __proto__: Array(0)

            }
        })

    }, [longitude, latitude]);

    return(
        <>
        <div id="wrap">
        <div id="header">
            <div className="headerTop">
                <div className="leftArea">
                    <a onClick={onClickBackBtn} className="icon pageBack">Back</a>
                </div>            
                <div className="middleArea">
                    <h1 className="headerTitle">  </h1>
                </div>
            </div>
        </div>
        <div id="container">
            <div id="content">
                <div className="">
                    <div className="mapSearch">
                        <div className="mapArea" id="myMap" > </div>
                        <div className="mapAddress">
                            <p className="addressMain">{userAddr}</p>
                            <p className="addressDetail">[] {userRoadAddr}</p>
                            <div className="addressInput">
                                <span className="textInput">
                                    <input type="text" title="" placeholder=" (, / )" />
                                </span>
                            </div>
                            <div className="btnWrap fullWidth topBlock">
                                <button type="submit" className="btn login default"><strong></strong></button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
        </>
    )
};
