import React, {useRef,createRef,useState, useCallback, useEffect} from 'react';
import Swiper from 'react-id-swiper';
// Version >= 2.4.0
import 'swiper/css/swiper.css';
import { Link, Redirect } from 'react-router-dom';

import * as ACTION from '../../common/ActionTypes'
import useAsync from '../../hook/useAcync' 
import * as API from '../../Api'
import Image from '../../components/Image';

 
export default function Home(){

    const [latitude, setLatitude] = useState(); 
    const [longitude, setLongitude] = useState(); 

    useEffect(()=>{
        // Geolocation API    
        if (navigator.geolocation) {
            //  
            navigator.geolocation.getCurrentPosition (function(pos) {
                setLatitude(pos.coords.latitude);
                setLongitude(pos.coords.longitude);
            });
        } else {
            alert("  Geolocation  .")
        }
    },[])
   
    return(
        <>
            <div id="wrap">
                <Header latitude = {latitude} longitude = {longitude} />
                <div id="container">
                    <div id="content">
                        <BannerSection/>
                        <CategorySection latitude = {latitude} longitude = {longitude}/>
                        <NewStoreSection/>
                    </div>
                </div>
                <Footer/>
            </div>
        </>
    );
}

const Header = ()=>{

    const [testRef, setTestRef] = useState(() => createRef())

    return (
    <div id="header">
        <div className="headerTop">
            <div className="leftArea">
                <Link to = {{pathname:ACTION.LINK_ZERO_PAY}} ref={testRef} className="icon zeroPay">Zero Pay</Link>
            </div>            
            <div className="middleArea">
                <Link to={{pathname: ACTION.LINK_SET_ADDRESS}} ref={testRef} className="addressBox">  97</Link>
                {/* active   */}
                <div className="tossPop active">
                    <div className="inner">
                        <p>  .</p>
                    </div>
                </div>
            </div>
            <div className="rightArea">
                <a ref={testRef} className="icon qrCode">QR Code</a>
            </div>
        </div>
    </div>
    );
}

const SimpleSwiperWithParams = () => {
    const params = {
      slidesPerView: 'auto',
      pagination: {
        el: '.swiper-pagination',
        type: 'fraction',
        clickable: true,
      },
      autoplay: {
        delay: 4000,
        disableOnInteraction: false
      },
      loop: true,
    }
   
    return(
        <>
            <Swiper {...params}>
                <div>
                    <Link to={{
                        pathname : ACTION.LINK_BANNER_DETAIL
                    }}>
                        <img src="/common/images/banner/img_tump_banner_01.png" alt="      " />
                    </Link>
                </div>
                <div>
                    <Link to={{
                        pathname : ACTION.LINK_BANNER_DETAIL
                    }}>
                        <img src="/common/images/banner/img_tump_banner_01.png" alt="      " />
                    </Link>
                </div>
                <div>
                    <Link to={{
                        pathname : ACTION.LINK_BANNER_DETAIL
                    }}>
                        <img src="/common/images/banner/img_tump_banner_01.png" alt="      " />
                    </Link>
                </div>
                <div>
                    <Link to={{
                        pathname : ACTION.LINK_BANNER_DETAIL
                    }}>
                        <img src="/common/images/banner/img_tump_banner_01.png" alt="      " />
                    </Link>
                </div>
            </Swiper>
            <Link className="viewAll" to={{
                pathname : ACTION.LINK_BANNER_LIST
            }}>
                 
            </Link>
        </>
    )
  }

const BannerSection = ()=>{

    return(
            <div className="mainBanner">
                <SimpleSwiperWithParams/>
            </div>
    );
}


const CategorySection = ({latitude , longitude})=>{
    console.log("--------------------------")
    console.log(latitude, longitude)
    const [categoryData, setCategoryData] = useState([]);

    const inputSearchRef = useRef();

    const [state,refetch] = useAsync(API.getCatogories,[]);
    const{loading, data,error} = state;


    if (loading) return (
        <div className="categorySection">
            <div className="stateWrap">
                <div className='loading'>..</div>
            </div>
        </div>
    );
    if (error) return (
        <div className="categorySection">
            <div className="stateWrap">
                <div className='error'> </div>
            </div>
        </div>
    );
    if (!data) return null;

    return(
        <div className="categorySection">
            <div className="categoryArea">
                <form>
                    <div className="searchBox">
                        <Link to={{pathname : ACTION.LINK_SEARCH, state : {latitude : latitude , longitude : longitude}}}>
                            <input type="text" className="searchInput" placeholder=",   " ref = {inputSearchRef}/>
                            <button className="searchBtn"></button>
                        </Link>
                        {/* <Link to={ACTION.LINK_SEARCH} className="searchBtn">
                            
                        </Link> */}
                    </div>
                </form>
                <ul className="categoryMenu">
                    {data.data.map((category) => {
                        // console.log(category);
                        return (
                            <li key={category.bizCtgDtlNmMi} className={"menu_" + category.bizCtgDtl}>
                                <Link to= {{pathname : ACTION.LINK_MARKET, state : { latitude : latitude , longitude : longitude , bizCtgDtl : category.bizCtgDtl}}}>{category.bizCtgGrpNm}</Link>
                            </li>
                        )
                    })}
                    
                </ul>
            </div>
        </div>
    );
}

const NewStoreSection = ()=>{
    const params = {
        slidesPerView: 1.25,
        freeMode: true,
        centeredSlides: false,
        spaceBetween: 7,
        initialSlide:0,
        
      }
    const [state,refetch] = useAsync(API.getNewStores,[]);
    const {loading, data, error} = state;
    console.log(state)

    if (loading) return (
        <div className="">
            <div className="stateWrap">
                <div className='loading'>..</div>
            </div>
        </div>
    );
    if (error) return (
        <div className="">
            <div className="stateWrap">
                <div className='error'> </div>
            </div>
        </div>
    );
    if (!data) return null;

    return(
        <div className="rollingSection">
            <h2 className="title"> </h2>
            <div className="productList">
                <Swiper {...params} >
                    {
                        data.data.map((store)=>{
                            return  <NewStoreItem key={store.strId} storeData = {store} />
                        })
                    }
                </Swiper>
                
            </div>
        </div>
    );
}





const NewStoreItem = ({storeData}) => {
    return (
            <div className="swiper-slide">
                <div className="productImg">
                    <Image src={storeData.imgModNm} alt={storeData.strNm} />
                </div>
                <div className="productInfo">
                    <p className="label">
                        {storeData.dlvYn === 'Y' && <span className="deli"></span>}
                        {storeData.pickYn === 'Y' && <span className="deli"></span>}
                    </p>
                    <p className="title">{storeData.strNm}</p>
                    <p className="desc"><span>  .</span></p>
                </div>    
            </div>
    )
}

const Footer = () => {
    
    const [toggleActive, setTggleActive] = useState(false)
    const fnToggleBtn = () => {
        if(!toggleActive){
            setTggleActive(true)
        }else{
            setTggleActive(false)
        }
    }
    const [testRef, setTestRef] = useState(() => createRef())
    
    
    return (
        <div id="footer">
            <div className="noticeInner">
                
                <p className="noticeMsg">
                    <Link to={ACTION.LINK_NOTICE_DETAIL}><span className="label"></span>   .</Link>
                </p>
                
            </div>
            <div className="footerInner">
                <div className="footerCompany">
                    <p className={(!toggleActive) ? "title fnToggleBtn" : "title fnToggleBtn active"}  onClick={fnToggleBtn}>KIS()</p>
                    <p className="desc fnToggleCon">
                         : , <br />
                         :    97,  22<br />
                         : 2019--1928<br />
                         : mobile@kisvan.co.kr <br />
                    </p>
                </div>
                <p className="footerMsg">
                    KIS    .<br />
                     KIS       .
                </p>
                <p className="footerLink">
                    <Link to={{pathname: ACTION.LINK_BUSINESS_INFO}} ref={testRef}></Link>
                    <span>|</span>
                    <Link to={{pathname: ACTION.LINK_SERVICE_TERMS}} ref={testRef}> </Link>
                    <span>|</span>
                    <Link to={{pathname: ACTION.LINK_SERVICE_TERMS}} ref={testRef}> </Link>
                    <span>|</span>
                    <Link to={{pathname: ACTION.LINK_SERVICE_TERMS}} ref={testRef}> </Link>
                </p>
            </div>
            
            <div id="nav">
                <div>
                    <ul className="navList">
                        <li className="home active"><Link to={{pathname: ACTION.LINK_HOME}} ref={testRef}></Link></li>
                        <li className="map"><Link to={{pathname: ACTION.LINK_AROUND_MAP}} ref={testRef}></Link></li>
                        <li className="myPage"><Link to={{pathname: ACTION.LINK_MYPAGE}} ref={testRef}>my</Link></li>
                        <li className="myOrder"><Link to={{pathname: ACTION.LINK_ORDER_HISTORY}} ref={testRef}></Link></li>
                        <li className="myLike"><Link to={{pathname: ACTION.LINK_MY_JJIM}} ref={testRef}></Link></li>
                    </ul>
                </div>
            </div>
        </div>
    );
    
}