import React, { useState } from 'react';

export default ({history}) => {

    const handleOnClick = (e) =>{
        e.preventDefault();
        history.goBack();
    }

    const fnToggleBtn = (e) =>{
        var items = document.querySelectorAll('.toggleList ul li')        
        if(e.currentTarget.parentNode.className  === "active"){
            e.currentTarget.parentNode.className = ""
        }else{
            for(var i =0; i < items.length; i++){
                items[i].className = "";
            }
            e.currentTarget.parentNode.className = "active"
        }
    }
    return (
        <div id="wrap">
            <div id="header">
                <div className="headerTop">
                    <div className="leftArea">
                        <a onClick={handleOnClick} className="icon pageBack">Back</a>
                    </div>
                    <div className="middleArea">
                        <h1 className="headerTitle">FAQ</h1>
                    </div>
                    {/* <!-- <div className="rightArea">
                        <a href="#" className="icon setting">
                            
                        </a>
                    </div> --> */}
                </div>
            </div>
            <div id="container">
                <div id="content">
                    <div className="">
                        <div className="tabBtnArea">
                            <ul className="btnList">
                                {/* <!-- active   --> */}
                                <li className="active">
                                    <a href="#"></a>
                                </li>
                                <li>
                                    <a href="#"></a>
                                </li>
                                <li>
                                    <a href="#"></a>
                                </li>
                                <li>
                                    <a href="#"></a>
                                </li>
                                <li>
                                    <a href="#"></a>
                                </li>
                                <li>
                                    <a href="#">/</a>
                                </li>
                            </ul>
                        </div>
                        <div className="toggleList">
                            <ul>
                                <li>
                                    <a href="#" onClick={(e) => {
                                        e.preventDefault()
                                        fnToggleBtn(e)
                                    }} className="toggleTitle fnToggleList">
                                        <p>   .</p>
                                    </a>
                                    <div className="toggleContent">
                                        <p className="contentMsg">
                                                 ( )        .<br />
                                                     .
                                                        (/)   .
                                        </p>
                                    </div>
                                </li>
                                <li>
                                    <a href="#" onClick={(e) => {
                                        e.preventDefault()
                                        fnToggleBtn(e)
                                    }} className="toggleTitle fnToggleList">
                                        <p>   ?</p>
                                    </a>
                                    <div className="toggleContent">
                                        <p className="contentMsg">
                                                 ( )        .
                                        </p>
                                    </div>
                                </li>
                                <li>
                                    <a href="#" onClick={(e) => {
                                        e.preventDefault()
                                        fnToggleBtn(e)
                                    }} className="toggleTitle fnToggleList">
                                        <p>    .</p>
                                    </a>
                                    <div className="toggleContent">
                                        <p className="contentMsg">
                                                 ( )        .<br />
                                                     .
                                                        (/)   .
                                        </p>
                                    </div>
                                </li>
                                <li>
                                    <a href="#" onClick={(e) => {
                                        e.preventDefault()
                                        fnToggleBtn(e)
                                    }}  className="toggleTitle fnToggleList">
                                        <p>   ?</p>
                                    </a>
                                    <div className="toggleContent">
                                        <p className="contentMsg">
                                                 ( )        .<br />
                                                     .
                                                        (/)   .
                                        </p>
                                    </div>
                                </li>
                                <li>
                                    <a href="#" onClick={(e) => {
                                        e.preventDefault()
                                        fnToggleBtn(e)
                                    }}  className="toggleTitle fnToggleList">
                                        <p>   .</p>
                                    </a>
                                    <div className="toggleContent">
                                        <p className="contentMsg">
                                                 ( )        .<br />
                                                     .
                                                        (/)   .
                                        </p>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            {/* <!-- <div id="nav">
                <div>
                    <ul className="navList">
                        <li className="home active"><a href="#"></a></li>
                        <li className="map"><a href="#"></a></li>
                        <li className="myPage"><a href="#">my</a></li>
                        <li className="myOrder"><a href="#"></a></li>
                        <li className="myLike"><a href="#"></a></li>
                    </ul>
                </div>
            </div> --> */}
        </div>
    )
};