import React, { useEffect, useState, useCallback, useRef } from 'react';
import { Link } from 'react-router-dom';

import * as ACTION from '../../../common/ActionTypes'

//  x
//   : common <-  or  ( )

const orderHistoryList = [
    {
        index: 0,
        DP: 0, 
        status: 0,    // 0:  
        time: ' 5:20  '  ,
        name: '  ',
        menu: ['', '', ''],
        price: 8800
      },
      {
        index: 1,
        DP: 0, 
        status: 1,    //1:  
        time: '2020.05.18  08:10'  ,
        name: '  ',
        menu: ['', '', '', ''],
        price: 12000
      },
      {
        index: 2,
        DP: 1, 
        status: 2,    // 2:  
        time: ' 10:10'  ,
        name: '  ',
        menu: [''],
        price: 9000
      },
      {
        index: 3,
        DP: 1, 
        status: 3,    // 3:  
        time: '2020.05.17  12:10'  ,
        name: '  ',
        menu: ['', ''],
        price: 13000
      },
      

  ];

  
  export default ( {history} ) => {
      
    const [historyData, setHistoryData] = useState([]);
    const [filter, setFilter] = useState(2);

    useEffect(() => {
        setHistoryData([...orderHistoryList]);
    }, [])

    const doFilter = (DP) => {
        const style = {
            display: 'none'
        }
        if(DP === filter) return '{}';
        else return style;
    }

    return (
        <div id="wrap">
            <div id="header">
                <div className="headerTop">
                    <div className="middleArea">
                        <h1 className="headerTitle"></h1>
                    </div>
                    <div className="rightArea">
                        <a href="#" className="icon reflash">
                            
                        </a>
                    </div>
                </div>
            </div>
            <div id="container">
                <div id="content">
                    <div className="fullHeight">
                        <div className="listSort pageSort">
                            <div className="btnWrap leftCol">
                                {/* <!-- active   &  --> */}
                                <button type="button" className={filter === 2 ? ('btn icon star active') : 'btn'} onClick={()=>setFilter(2)}></button>
                                <button type="button" className={filter === 0 ? ('btn icon star active') : 'btn'} onClick={()=>setFilter(0)}></button>
                                <button type="button" className={filter === 1 ? ('btn icon star active') : 'btn'} onClick={()=>setFilter(1)}></button>
                            </div>                      
                        </div>
                        <div className="sectionBlock" ></div>
                        { historyData.map((data) => 
                        <>
                            <div  key={data.index}className="orderInfoList typeResult" style={{display: (data.DP === filter || filter === 2) ? 'block': 'none'}}>
                                { <SingleHistoryComponent data={data} /> }
                            </div>
                            <div className="sectionBlock" style={{display: (data.DP === filter|| filter === 2)? 'block': 'none'}}></div>
                        </>
                        )}

                    </div>
                </div>
            </div>
            <div id="nav">
                <div>
                <ul className="navList">
                    <li className="home"><Link to={{pathname: ACTION.LINK_HOME}}></Link></li>
                    <li className="map"><Link to={{pathname: ACTION.LINK_AROUND_MAP}}></Link></li>
                    <li className="myPage"><Link to={{pathname: ACTION.LINK_MYPAGE}}>my</Link></li>
                    <li className="myOrder active"><Link to={{pathname: ACTION.LINK_ORDER_HISTORY}}></Link></li>
                    <li className="myLike"><Link to={{pathname: ACTION.LINK_MY_JJIM}}></Link></li>
                </ul>
                </div>
            </div>
        </div>
    );
};

const SingleHistoryComponent = ( {data} ) => {

    const timeSwitch = (status) => {
        switch(status) {
            case 0:
                return (
                <>
                    
                    <a href="#" className="moreDate orderIng">{data.time}</a>
                </>
                );
            case 1:
                return (
                <>
                     
                    <a href="#" className="moreDate">{data.time}</a>

                </>
                );
            case 2:
                return (
                <>
                     
                    <a href="#" className="moreDate orderIng">{data.time} </a>
                </>
                );
            case 3:
                return (
                <>
                     
                    <a href="#" className="moreDate">{data.time}</a>
                </>
                );
                default:

                break

        }
    }

    const btnSwitch = (status) => {
        switch(status) {
            case 0:
            case 2:
                return (
                <> 
                    <ul className="btnInner">
                        <li>
                            <Link to={{pathname: ACTION.LINK_ORDER_DETAIL
                                /* , state:  {
                                    data: history index 
                                }
                                */
                            }} className="btn icon detail">
                                
                            </Link>
                        </li>
                    </ul>
                </>
                );
            case 1:
            case 3:
                return (
                <>
                    <ul className="btnInner">
                        <li><Link to={{pathname: ACTION.LINK_MAKE_REVIEW}} className="btn icon review"></Link></li>
                        {/* <!--  disable     --> */}
                        <li>
                            <Link to={{pathname: ACTION.LINK_ORDER_DETAIL
                            /* , state:  {
                                data: history index 
                            }
                            */
                            }} className="btn icon detail"></Link>
                        </li>
                    </ul>
                </>
                );
                default:

                break
        }
    }

    return (
        <>
            <h2 className="listTitle">
                <div className="statusLabel">
                {data.DP === 0 ?
                    <span className="label deli"></span>
                    : <span className="label pick"></span>
                }
                </div>
                { timeSwitch(data.status) }
            </h2>
            <div className="infoBox">
                <div className="infoInner">
                    <p className="title"><strong>{data.name}</strong><a href="#" className="btnMenu"></a></p>
                    <p className="option">{data.menu[0]} {data.menu.length > 1 ? ('  '+(data.menu.length-1)+'') : ''} {data.price}</p>
                </div>
                <div className="btnWrap">
                    { btnSwitch(data.status) }
                </div>
            </div>
        </>
    )
}