import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';

import Joining_Header from '../../../components/HeaderCloser'

export default () => {
    return (
        <div id="wrap">
            <Joining_Header headerTitle=''/>
            <div id="container">
                <div id="content">
                    <div className="fullHeight">
                        <div className="sectionRow">
                            <div className="loginForm">
                                <div className="btnWrap">
                                    <strong className="btn login disable">  </strong>
                                </div>
                                <ul className="registerList">
                                    <li>
                                        <span className="leftCell">
                                            <label htmlFor="register_id"></label>
                                        </span>
                                        <span className="rightCell">
                                            <span className="textInput">
                                                <input type="email" id="register_id" placeholder="  "/>
                                            </span>
                                        </span>
                                    </li>
                                    <li>
                                        <span className="leftCell">
                                            <label htmlFor="register_pw"></label>
                                        </span>
                                        <span className="rightCell">
                                            <span className="textInput">
                                                <input type="password" id="register_pw" placeholder="++  8 "/>
                                            </span>
                                        </span>
                                    </li>
                                    <li>
                                        <span className="leftCell">
                                            <label htmlFor="register_ck"> </label>
                                        </span>
                                        <span className="rightCell">
                                            <span className="textInput">
                                                <input type="password" id="register_ck" placeholder="  "/>
                                            </span>
                                        </span>
                                    </li>
                                    <li>
                                        <span className="leftCell">
                                            <label htmlFor="register_nic"></label>
                                        </span>
                                        <span className="rightCell">
                                            <span className="textInput">
                                                <input type="text" id="register_nic" placeholder="2~10  "/>
                                            </span>
                                        </span>
                                    </li>
                                </ul>
                            </div>
                            <div className="fixedBottom">
                                <div className="btnWrap fullWidth">
                                    <Link to="/Joining_4">
                                        <button type="submit" className="btn login disable"><strong></strong></button>
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
