import React from 'react';
import { Link } from 'react-router-dom';

import WrapPage from "../../components/WrapPage"
import * as ACTION from '../../common/ActionTypes'

export default ({history}) => {

    const handleOnClick = (e) =>{
        e.preventDefault();
        history.goBack();
    }
    
    return (
    <WrapPage>
    <div id="wrap">
        <div id="header">
            <div className="headerTop">
                <div className="leftArea">
                    <a onClick={handleOnClick} className="icon pageBack">Back</a>
                </div>
                <div className="middleArea">
                    <h1 className="headerTitle"></h1>
                </div>
            </div>
        </div>
        <div id="container">
            <div id="content">
                <div className="fullHeight">
                    <div className="pageToplView">
                        <ul className="settingList">
                            <li>
                                <span> </span>
                                <label className="checkToggle">
                                    <input type="checkbox" title=" " />
                                    <span className="slider"></span>
                                </label>
                            </li>
                            <li>
                                <span>  </span>
                                <label className="checkToggle" title="  ">
                                    <input type="checkbox" />
                                    <span className="slider"></span>
                                </label>
                            </li>
                        </ul>
                    </div>
                    <div className="sectionBlock"></div>
                    <div className="arrowList">
                        <ul>
                            <li>
                                {console.log(ACTION.LINK_SERVICE_TERMS)}
                                <Link to={{pathname: ACTION.LINK_SERVICE_TERMS}} className="listLine">
                                     
                                </Link>
                            </li>
                            <li>
                                <a href="#" className="listLine">  </a>
                            </li>
                            <li className="msgBlock">
                                <span className="msgTxt">
                                      1.1.1.0
                                    <span className="moreMsg">  .</span>
                                </span>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>

        <div id="nav">
            <div>
                <ul className="navList">
                    <li className="home"><Link to={{pathname: ACTION.LINK_HOME}}></Link></li>
                    <li className="map"><Link to={{pathname: ACTION.LINK_AROUND_MAP}}></Link></li>
                    <li className="myPage active"><Link to={{pathname: ACTION.LINK_MY_SDL}}>my</Link></li>
                    <li className="myOrder"><Link to={{pathname: ACTION.LINK_ORDER_HISTORY}}></Link></li>
                    <li className="myLike"><Link to={{pathname: ACTION.LINK_MY_JJIM}}></Link></li>
                </ul>
            </div>
        </div>
    </div>
    </WrapPage>
    );
};