import React from 'react';
import { Link } from 'react-router-dom';

import * as ACTION from '../../common/ActionTypes'
import useAsync from '../../hook/useAcync'
import * as API from '../../Api'

export default () => {

    const [state, refech] = useAsync(() => {
        // API.signIn() //   
        //  403 , mypage_non redirect
    })

    return (
        <div id="wrap">
            <div id="header">
                <div className="headerTop">
                    <div className="middleArea">
                        <h1 className="headerTitle">My</h1>
                    </div>
                    <div className="rightArea">
                    <Link to={{pathname: ACTION.LINK_SETTING}} className="icon setting">
                        
                    </Link>
                </div>
                </div>
            </div>
            <div id="container">
                <div id="content">
                    <div className="fullHeight">


                        <div className="pageToplView">
                            <div className="userInfo">
                                <a href="#">
                                    <span className="userProfile"></span>
                                    <strong className="username"> </strong>
                                </a>
                            </div>
                        </div>


                        <div className="pageToplView">
                            <div>
                                <h1 className="logo">
                                       
                                </h1>  
                                <div className="btnWrap typeFlex">
                                    <ul className="btnArea">
                                        <li>
                                            <Link to={{pathname: ACTION.LINK_SIGNUP_AGREE}} className="btn register default">
                                                <strong></strong>
                                            </Link>
                                        </li>
                                        <li>
                                            <Link to={{pathname: ACTION.LINK_LOGIN}} className="btn login apply">
                                                <strong></strong>
                                            </Link>
                                        </li>
                                    </ul>
                                </div> 
                            </div>
                        </div>


                        <div className="sectionBlock"></div>
                        <div className="arrowList">
                            <ul>

                                <li>
                                    <Link to={{pathname: ACTION.LINK_REVIEW}} className="listLine"> </Link>
                                </li>
                                <li>
                                    <Link to={{pathname: ACTION.LINK_NOTICE_LIST}} className="listLine"></Link>
                                </li>
                                <li>
                                    <Link to={{pathname: ACTION.LINK_FAQ}} className="listLine">FAQ</Link>
                                </li>
                                <li>
                                    <Link to={{pathname: ACTION.LINK_CS}} className="listLine"></Link>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            {/* <FooterNav screenFocus="myPage"/> */}
            <div id="nav">
                <div>
                <ul className="navList">
                    <li className="home"><Link to={{pathname: ACTION.LINK_HOME}}></Link></li>
                    <li className="map"><Link to={{pathname: ACTION.LINK_AROUND_MAP}}></Link></li>
                    <li className="myPage active"><Link to={{pathname: ACTION.LINK_MYPAGE}}>my</Link></li>
                    <li className="myOrder"><Link to={{pathname: ACTION.LINK_ORDER_HISTORY}}></Link></li>
                    <li className="myLike"><Link to={{pathname: ACTION.LINK_MY_JJIM}}></Link></li>
                </ul>
                </div>
            </div>
        </div>
    )
}