import React from 'react';
import { Link } from 'react-router-dom';

import WrapPage from "../../components/WrapPage"

export default ({history}) => {
    
  const onClickBackBtn = (e) => {
    e.preventDefault();
    history.goBack();
  };

  return (
    <WrapPage>
    <div id="wrap">
        <div id="header">
            <div className="headerTop">
                <div className="leftArea">
                    <a onClick={onClickBackBtn} className="icon pageBack">Back</a>
                </div>
                <div className="middleArea">
                    <h1 className="headerTitle"> </h1>
                </div>
            </div>
        </div>
        <div id="container">
            <div id="content">
                <div className="">
                    <div className="tabBtnArea">
                        <ul className="btnList">
                            <li className="active">
                                <a href="#"> </a>
                            </li>
                            <li>
                                <a href="#"> </a>
                            </li>
                            <li>
                                <a href="#"> </a>
                            </li>
                            <li>
                                <a href="#"> </a>
                            </li>
                            <li>
                                <a href="#">  </a>
                            </li>
                        </ul>
                    </div>
                    <div className="agreeContent">
                        <div>
                                               
                                                                              
                                                                                                                                                                                   
                                                                                                             
                        </div>
                    </div>
                    <div className="selectArea">
                        <div className="selectWrap">
                            <select className="designSelect">
                                <option>   </option>
                            </select>
                        </div>
                    </div>
                    
                </div>
            </div>
        </div>
    </div>
    </WrapPage>
    );

}
