import React, { useEffect, useState, useCallback, useRef } from 'react';
import { Link } from 'react-router-dom';

import WrapPage from "../../../components/WrapPage"
import * as ACTION from '../../../common/ActionTypes'

//  x
//   : common <-  or  ( )

export const orderInfo = {
    userYN: "N",
    DP: "",
    marketInfo: {
                marketName: "  ",
                marketAddr: "   143"
                },
    userAddr: "   3-25 ",
    userPhone: "01098765432",
    menus: [
            {menuName: "",
            amount: 2,
            options: [" ", " "],
            price: 5600
            },
            {menuName: "",
            amount: 1,
            options: [""],
            price: 4900
            }
            ],
    delivTip: 2500,
    discounts: [
                {
                    discountName: " ",
                    discountAmount: 500
                },
                {
                    discountName: " ",
                    discountAmount: 1000
                }
            ],
}

export default ( {history} ) => {

    const [seconds, setSeconds] = useState(5);
    const timeouts = useRef();

    useEffect(() => {
        return () => {
            clearTimeout(timeouts.current)
            timeouts.current = [];
        }
    }, [])

    useEffect(() => {
        timeouts.current = setTimeout(() => {
            setSeconds(seconds -1);
        }, 1 * 1000);
    })

    useEffect(() => {
        if(seconds === 0) {
            history.push(ACTION.LINK_ORDER_HISTORY);
            // history.replace(ACTION.LINK_ORDER_HISTORY);
        }
    }, [seconds])

    return (
        <WrapPage>
            <div id="wrap">
            <div id="header">
                <div className="headerTop">
                    
                    <div className="leftArea">
                        <Link to = {{pathname: ACTION.LINK_HOME}} className="icon pageClose">CLOSE</Link>
                    </div>
                    
                    <div className="middleArea">
                        <h1 className="headerTitle"> </h1>
                    </div>
                </div>
            </div>
            <div id="container">
                <div id="content">
                    <div className="fullHeight">
                        <div className="msgBox">
                            <p className="orderComplate">
                                <strong>  </strong><br />
                                  <strong></strong>.
                            </p>                        
                        </div>
                        <div className="countMsg">
                            <p>
                                   <br />
                                <span>{seconds}</span>     .
                            </p>
                        </div>                    
                        <div className="orderInfoList">
                            <ul className="infoLIst">
                                <li>
                                    <strong className="leftCell"></strong>
                                    <span className="rightCell">
                                        <span>   97</span>
                                        <span className="moreInfo">2301</span>
                                    </span>
                                </li>
                                <li>
                                    <strong className="leftCell"></strong>
                                    <span className="rightCell">
                                        <span> x 2</span>
                                        <span className="options"> , </span>
                                    </span>
                                </li>
                                <li>
                                    <strong className="leftCell"></strong>
                                    <span className="rightCell">
                                        <strong className="amount">9,600</strong>
                                        <span className="moreInfo"> / </span>
                                    </span>
                                </li>
                            </ul>
                        </div>
                        <p className="bottomMsg">
                             ,     .<br />
                                   .
                        </p>
                    </div>
                    <div className="fixedBtn">
                        <a href="#" className="btn addCart"></a>
                        <Link to = {{pathname:ACTION.LINK_ORDER_HISTORY}} className="btn addOrder"> </Link>
                    </div>
                </div>
            </div>
        </div>
        </WrapPage>
    );
};